___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --primary: #3a86ff;
    --secondary: #8338ec;
    --accent: #ff006e;
    --light: #f8f9fa;
    --dark: #212529;
    --success: #2ecc71;
    --warning: #f39c12;
    --danger: #e74c3c;
    --card-bg: rgba(255, 255, 255, 0.15);
    --text-color: #333;
    --primary-background: #667eea;

}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background: var(--primary-background);
    color: var(--light);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 1rem;
}

/* Header */

.header {
    top: 0;
    width: 100%;
    display: grid;
    grid-template-columns: 3fr 1fr;
    justify-content: space-between;
    margin-top: 10px;
}

.left-header {
    margin-left: clamp(10px,30px,40px);
    justify-self: start;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    font-size: clamp(0.8rem, 1.8vw, 1.8rem);
    font-weight: 600;
    text-align: center;
    color: var(--light);
    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}

.right-header {
    margin-right: 30px;
    justify-self: self-end;
}

/* Main wrapper */
.weather-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
}

/* Search area */
.search-box {
    margin: 1rem auto;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    flex-wrap: wrap;
}

/* Address */
.address {
    font-size: clamp(2rem, 5vw, 3.5rem);
    font-weight: 600;
    text-align: center;
    color: var(--light);
    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}

/* Weather container */
.primary-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--card-bg);
    border-radius: 20px;
    padding: 2rem;
    min-height: 250px;
    width: 100%;
    max-width: 500px;
    text-align: center;
    gap: 1rem;
    backdrop-filter: blur(10px);
}


/* Input field */
input {
    background: var(--card-bg);
    padding: 12px 20px;
    border: 1px solid rgba(255, 255, 255, 0.4);
    border-radius: 30px;
    font-size: 1rem;
    flex: 1;
    min-width: 200px;
    color: var(--light);
}

input:focus {
    outline: none;
    border-color: var(--accent);
}

/* Button */
button {
    background-color: var(--primary);
    color: var(--light);
    padding: 12px 24px;
    font-size: 1rem;
    border: none;
    border-radius: 30px;
    cursor: pointer;
}

button:hover {
    background-color: var(--secondary);
}

/* Weather icon */
.icons {
    width: clamp(100px, 25vw, 150px);
    height: auto;
    display: block;
    margin: 0 auto;
}

/* Tempreture */
.temp {
    font-size: clamp(2.5rem, 6vw, 4rem);
    font-weight: bold;
    color: var(--light);
}

/* details container */
.details-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.5rem;
    font-size: clamp(0.8rem, 2vw, 1rem);
    opacity: 0.9;
    margin-top: 0.5rem;
}

.details-container div {
    background: rgba(255, 255, 255, 0.1);
    padding: 8px 10px;
    border-radius: 8px;
    min-width: 110px;
}

/* Forecast container */
.forecast {
    font-size: clamp(0.8rem, 1.8vw, 1.8rem);
    font-weight: 600;
    text-align: center;
    color: var(--light);
    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}

.secondary-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 1rem;
}

/* Forecast cards */
.forecast-wrapper {
    flex: 1 3 clamp(150px, 20%, 220px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--card-bg);
    border-radius: 20px;
    padding: 1rem;
    text-align: center;
    gap: 0.5rem;
    min-height: 180px;
}

/* Forecast icons */
.forecast-icons {
    width: clamp(50px, 10vw, 80px);
    height: auto;
}

/* Forecast temperature */
.forecast-temp {
    font-size: clamp(1.2rem, 2vw, 1.5rem);
    font-weight: bold;
    color: var(--light);
}

/* Forecast conditions */
.forecast-conditions,.forecast-date {
    font-size: clamp(0.8rem, 1.5vw, 1rem);
    color: var(--light);
    opacity: 0.9;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,kBAAkB;IAClB,oBAAoB;IACpB,iBAAiB;IACjB,gBAAgB;IAChB,eAAe;IACf,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;IACjB,oCAAoC;IACpC,kBAAkB;IAClB,6BAA6B;;AAEjC;;AAEA;IACI,4DAA4D;IAC5D,qCAAqC;IACrC,mBAAmB;IACnB,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,iBAAiB;IACjB,aAAa;AACjB;;AAEA,WAAW;;AAEX;IACI,MAAM;IACN,WAAW;IACX,aAAa;IACb,8BAA8B;IAC9B,8BAA8B;IAC9B,gBAAgB;AACpB;;AAEA;IACI,kCAAkC;IAClC,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;IACT,uCAAuC;IACvC,gBAAgB;IAChB,kBAAkB;IAClB,mBAAmB;IACnB,yCAAyC;AAC7C;;AAEA;IACI,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA,iBAAiB;AACjB;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,WAAW;AACf;;AAEA,gBAAgB;AAChB;IACI,iBAAiB;IACjB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;IACT,eAAe;AACnB;;AAEA,YAAY;AACZ;IACI,mCAAmC;IACnC,gBAAgB;IAChB,kBAAkB;IAClB,mBAAmB;IACnB,yCAAyC;AAC7C;;AAEA,sBAAsB;AACtB;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,0BAA0B;IAC1B,mBAAmB;IACnB,aAAa;IACb,iBAAiB;IACjB,WAAW;IACX,gBAAgB;IAChB,kBAAkB;IAClB,SAAS;IACT,2BAA2B;AAC/B;;;AAGA,gBAAgB;AAChB;IACI,0BAA0B;IAC1B,kBAAkB;IAClB,0CAA0C;IAC1C,mBAAmB;IACnB,eAAe;IACf,OAAO;IACP,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,2BAA2B;AAC/B;;AAEA,WAAW;AACX;IACI,gCAAgC;IAChC,mBAAmB;IACnB,kBAAkB;IAClB,eAAe;IACf,YAAY;IACZ,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,kCAAkC;AACtC;;AAEA,iBAAiB;AACjB;IACI,gCAAgC;IAChC,YAAY;IACZ,cAAc;IACd,cAAc;AAClB;;AAEA,eAAe;AACf;IACI,mCAAmC;IACnC,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA,sBAAsB;AACtB;IACI,aAAa;IACb,eAAe;IACf,uBAAuB;IACvB,WAAW;IACX,mCAAmC;IACnC,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,oCAAoC;IACpC,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA,uBAAuB;AACvB;IACI,uCAAuC;IACvC,gBAAgB;IAChB,kBAAkB;IAClB,mBAAmB;IACnB,yCAAyC;AAC7C;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;IACf,SAAS;IACT,WAAW;IACX,iBAAiB;IACjB,cAAc;IACd,aAAa;AACjB;;AAEA,mBAAmB;AACnB;IACI,kCAAkC;IAClC,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,0BAA0B;IAC1B,m